"use client"
import { Search, User, ShoppingCart, Phone, MapPin } from "lucide-react"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { useCart } from "@/lib/context/cart-context"
import { useAuthenticator } from "@aws-amplify/ui-react"
import { useEffect, useState } from "react"
import { chinaWokApi } from "@/lib/chinaWokApi"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from "@/components/ui/dialog"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { Label } from "@/components/ui/label"
import { Input } from "@/components/ui/input"

export function Header() {
  const { total } = useCart()
  const { user } = useAuthenticator((context) => [context.user])
  const [selectedAddress, setSelectedAddress] = useState<string>("")
  const [isAddressModalOpen, setIsAddressModalOpen] = useState(false)
  const [addresses, setAddresses] = useState<string[]>([])
  const [newAddress, setNewAddress] = useState("")

  useEffect(() => {
    async function fetchAddresses() {
      if (user?.username) {
        try {
          const profile = await chinaWokApi.getAddresses(user.username)
          if (profile?.addresses) {
            setAddresses(profile.addresses)
          }
        } catch (error) {
          console.error("Failed to fetch addresses:", error)
        }
      }
    }
    fetchAddresses()
  }, [user])

  const handleAddressSelect = (address: string) => {
    setSelectedAddress(address)
    setIsAddressModalOpen(false)
    // TODO: Guardar `selectedAddress` en un contexto global
    // para que el carrito de compras lo pueda usar.
  }

  const handleAddNewAddress = async () => {
    if (newAddress && user?.username) {
      try {
        await chinaWokApi.saveAddress(user.username, newAddress, "Casa") // 'Casa' es un alias por defecto
        setAddresses([...addresses, newAddress])
        setNewAddress("")
        handleAddressSelect(newAddress)
      } catch (error) {
        console.error("Failed to save address:", error)
      }
    }
  }

  return (
    <header className="sticky top-0 z-50 bg-white shadow-sm">
      <div className="container mx-auto flex h-20 items-center justify-between px-4">
        <Link href="/" className="flex items-center">
          <div className="relative h-10 w-40">
            <img
              src="https://www.chinawok.com.pe/assets/images/logo-chinawok.svg"
              alt="China Wok"
              className="h-full w-full object-contain"
            />
          </div>
        </Link>
        <nav className="hidden items-center gap-8 md:flex">
          <Link
            href="/menu"
            className="flex items-center gap-2 text-sm font-medium text-gray-700 hover:text-cw-red"
          >
            <img src="/bowl.jpg" alt="" className="h-6 w-6" />
            Men√∫
          </Link>
          <Link
            href="/menu"
            className="flex items-center gap-2 text-sm font-medium text-gray-700 hover:text-cw-red"
          >
            <img src="/discount-sign.png" alt="" className="h-6 w-6" />
            Promos exclusivas
          </Link>
          <Link
            href="/locales"
            className="flex items-center gap-2 text-sm font-medium text-gray-700 hover:text-cw-red"
          >
            <MapPin className="h-5 w-5" />
            Locales
          </Link>
          <button className="text-gray-700 hover:text-cw-red">
            <Search className="h-5 w-5" />
          </button>
        </nav>
        <div className="flex items-center gap-6">
          <div className="hidden items-center gap-2 text-sm md:flex">
            <Phone className="h-4 w-4" />
            <div className="flex flex-col leading-tight">
              <span className="text-xs text-gray-500">Ll√°manos</span>
              <a href="tel:016128000" className="font-bold text-cw-green">
                01 - 612 - 8000
              </a>
            </div>
          </div>

          <Link
            href={user ? "/customer/account" : "/customer/account/login"}
            className="hidden items-center gap-2 md:flex hover:opacity-80 transition-opacity"
          >
            <User className="h-6 w-6 text-gray-600" />
            <div className="flex flex-col leading-tight text-sm">
              <span className="text-gray-500">Hola,</span>
              <span className="font-bold text-cw-green">
                {user ? user.username || user.attributes?.email : "INICIAR SESI√ìN"}
              </span>
            </div>
          </Link>

          <Link href="/checkout/cart">
            <Button className="bg-cw-green hover:bg-green-700 text-white rounded-full px-6">
              <ShoppingCart className="mr-2 h-4 w-4" />
              S/ {total.toFixed(2)}
            </Button>
          </Link>
        </div>
      </div>
      <div className="border-t border-gray-100">
        <div className="container mx-auto flex items-center justify-between px-4 py-2">
          <div className="flex items-center gap-4">
            <Button
              className="bg-cw-green hover:bg-green-700 text-white rounded-full h-8 text-xs px-4 font-normal"
              onClick={() => user && setIsAddressModalOpen(true)}
              disabled={!user}
            >
              <MapPin className="mr-2 h-3 w-3" />
              {selectedAddress
                ? `üìç Entrega en: ${selectedAddress}`
                : "¬°Comienza tu pedido! Elige tu direcci√≥n"}
            </Button>
          </div>
          <div className="hidden md:flex items-center gap-4 text-xs text-gray-500">
            <button className="flex items-center gap-1 hover:text-cw-red">
              <span className="text-red-500">‚ô°</span> Mis Favoritos
            </button>
            <Link
              href="/tracking/order/index"
              className="flex items-center gap-1 hover:text-cw-red"
            >
              <span>üïí</span> Sigue tu pedido
            </Link>
            <Link href="/orders" className="flex items-center gap-1 hover:text-cw-red">
              <span>üìã</span> Mis Pedidos
            </Link>
          </div>
        </div>
      </div>
      <div className="bg-cw-red py-1.5 text-center text-xs font-medium text-white">
        ENVIO GRATIS DE LUNES A JUEVES por compras mayores a S/24.90 | EXCLUSIVO POR WEB
      </div>

      <Dialog open={isAddressModalOpen} onOpenChange={setIsAddressModalOpen}>
        <DialogContent className="sm:max-w-[425px]">
          <DialogHeader>
            <DialogTitle>Elige o agrega una direcci√≥n</DialogTitle>
          </DialogHeader>
          <div className="grid gap-4 py-4">
            <RadioGroup onValueChange={handleAddressSelect} value={selectedAddress}>
              {addresses.map((address) => (
                <div key={address} className="flex items-center space-x-2">
                  <RadioGroupItem value={address} id={address} />
                  <Label htmlFor={address}>{address}</Label>
                </div>
              ))}
            </RadioGroup>
            <div className="mt-4 border-t pt-4">
              <h4 className="font-medium mb-2">Agregar nueva direcci√≥n</h4>
              <div className="flex gap-2">
                <Input
                  value={newAddress}
                  onChange={(e) => setNewAddress(e.target.value)}
                  placeholder="Ej: Av. La Marina 123"
                />
                <Button onClick={handleAddNewAddress}>Agregar</Button>
              </div>
            </div>
          </div>
          <DialogFooter>
            <Button variant="secondary" onClick={() => setIsAddressModalOpen(false)}>
              Cerrar
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </header>
  )
}
